@import '~node_modules/modern-normalize/modern-normalize.css';
:root {
  --timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  --animation-duration: 250ms;
}

.gallery {
  display: grid;
  max-width: 1140px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  grid-gap: 12px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 10px;
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.gallery__link {
  display: block;
  text-decoration: none;
  height: 100%;
}

.gallery__link:hover {
  cursor: zoom-in;
}

p {
  margin-block: 0;
}

.photo-card {
  position: relative;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.2), 0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
  transition: transform var(--animation-duration) var(--timing-function);
  border: 1px solid #ffffff;
}
.photo-card:hover {
  transform: scale(1.03);
}

.info {
  position: absolute;
  bottom: 0;
  z-index: 10;
  display: flex;
  gap: 20px;
  align-items: center;
  width: 100%;
  height: 40px;
  padding: 10px;
  font-size: 12px;
  color: #cccfd2;
  color: #ffffff;
  border-top: 1px solid #ffffff;
  background-color: #002157;
}
.info-item {
  flex-basis: calc((100% - 20px) / 4);
}

.form-wrapper {
  width: 100%;
  background-color: #002157;
}

#search-form {
  display: flex;
  justify-content: center;
  gap: 10px;
  width: auto;
  padding-block: 10px;
}
#search-form input {
  min-width: 200px;
  height: 30px;
  padding-inline: 5px;
  border: none;
  border-radius: 7px;
  text-align: center;
}
#search-form input::placeholder {
  font-weight: 300;
  text-align: center;
}

button {
  min-width: 100px;
  height: 30px;
  padding: 0;
  border: none;
  border-radius: 7px;
  font-weight: 400;
  line-height: 1;
  text-transform: uppercase;
  cursor: pointer;
  transition: color 250ms, background-color 250ms;
}
button:hover, button:focus {
  color: rgba(0, 0, 0, 0.8);
  background-color: #ffbe2f;
}
button.load-more {
  display: block;
  margin-block: 10px 30px;
  margin-inline: auto;
  border: 1px solid rgba(0, 0, 0, 0.8);
}
button.load-more.hidden {
  display: none;
}

body {
  font-family: "Roboto Condensed", sans-serif;
  background-color: #767676;
}